/* eslint-disable no-undef */
/* eslint-disable @typescript-eslint/consistent-type-imports */
import type { AfterViewInit } from '@angular/core';
import { Component } from '@angular/core';
import { NavigationStart, Router } from '@angular/router';
import { environment } from 'src/environments/environment';
import { config } from './shared/app.config';
import { SessionService } from './shared/services/session-service/session.service';

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.scss'],
})
export class AppComponent implements AfterViewInit {
  title = 'LynxApp';
  constructor(private router: Router, private session: SessionService) {
    router.events.forEach((event) => {
      if (event instanceof NavigationStart) {
        this.session.currentLocation = event.url;
      }
    });
  }

  ngAfterViewInit() {
    const appRootElement = document.getElementsByTagName('app-root')[0];
    const routePath = appRootElement?.id;
    const baseUrl = appRootElement?.attributes.getNamedItem('name')?.value;
    const requestRate = appRootElement?.attributes.getNamedItem('requestrate')?.value;
    const culture = appRootElement?.attributes.getNamedItem('culture')?.value;
    const currentTenant = appRootElement?.attributes.getNamedItem('currentTenant')?.value;
    const timeZone = appRootElement?.attributes.getNamedItem('timeZone')?.value;
    const lengthUnit = appRootElement?.attributes.getNamedItem('lengthUnit')?.value;
    const colors: string = appRootElement?.attributes.getNamedItem('colors')?.value;
    if (baseUrl) {
      environment.APIUrl = baseUrl;
    }
    if (requestRate) {
      config.requestRate = requestRate;
    }
    if (routePath.length > 0) {
      this.router.navigate([`/${routePath}`]);
    } else if (this.session.currentLocation && this.session.currentLocation !== '/') {
      this.router.navigate([this.session.currentLocation]);
    }
    if (currentTenant) {
      this.session.currentTenant = JSON.parse(currentTenant);
    }
    if (timeZone) {
      this.session.timeZone = timeZone;
    }
    if (lengthUnit) {
      this.session.lengthUnit = lengthUnit;
    }

    if (colors) {
      this.session.colors = colors.split(',');
    }

    this.session.registerCulture(culture ?? 'en');
  }
}
