import { DOCUMENT, registerLocaleData } from '@angular/common';
import localeDeutsh from '@angular/common/locales/de';
import localeSchweizDeutsh from '@angular/common/locales/de-CH';
import localEnglish from '@angular/common/locales/en';
import localeGb from '@angular/common/locales/en-GB';
import localeES from '@angular/common/locales/es';
import localeESUnidos from '@angular/common/locales/es-US';
import localeFR from '@angular/common/locales/fr';
import localeFRBF from '@angular/common/locales/fr-BF';
import { inject, Inject, Injectable, InjectionToken } from '@angular/core';

@Injectable({ providedIn: 'root' })
export class SessionService {
  private _locale = '';
  private _colors = [];
  private _timeZone = '';
  private _lengthUnit = 'km';
  private _timeFormat: '24' | '12' = '24';

  public currentTenant: {
    id: number;
    name: string;
    currency?: string;
  };

  constructor(@Inject(LOCAL_STORAGE) private localStorage: Storage) {}

  get timeFormat(): '24' | '12' {
    this._timeFormat = this.checkTimeFormat() ? '12' : '24';
    return this._timeFormat;
  }

  get lengthUnit(): string {
    return this._lengthUnit;
  }

  set lengthUnit(value: string) {
    this._lengthUnit = value;
  }

  get timeZone(): string {
    return this._timeZone || 'UTC';
  }

  set timeZone(value: string) {
    this._timeZone = value;
  }

  get locale(): string {
    return this._locale || 'en-US';
  }
  set locale(value: string) {
    this._locale = value;
  }

  get currentLocation(): string {
    return JSON.parse(this.localStorage.getItem('ngCurrentUrl'));
  }

  set currentLocation(url: string) {
    this.localStorage.setItem('ngCurrentUrl', JSON.stringify(url));
  }

  get colors(): string[] {
    return this._colors;
  }
  set colors(value: string[]) {
    this._colors = value;
  }

  checkTimeFormat() {
    const options = new Intl.DateTimeFormat(this.locale, {
      hour: 'numeric',
    }).resolvedOptions();
    return options.hour12 ?? false;
  }

  registerCulture(culture: string): void {
    if (!culture) {
      return;
    }
    this.locale = culture;
    // Register locale data since only the en-US locale data comes with Angular
    switch (culture) {
      case 'en': {
        registerLocaleData(localEnglish);
        break;
      }
      case 'en_GB': {
        registerLocaleData(localeGb);
        break;
      }
      case 'de': {
        registerLocaleData(localeDeutsh);
        break;
      }
      case 'de-CH': {
        registerLocaleData(localeSchweizDeutsh);
        break;
      }
      case 'es': {
        registerLocaleData(localeES);
        break;
      }
      case 'es-US': {
        registerLocaleData(localeESUnidos);
        break;
      }
      case 'fr': {
        registerLocaleData(localeFR);
        break;
      }
      case 'fr-FR': {
        registerLocaleData(localeFRBF);
        break;
      }
    }
  }
}

export const LOCAL_STORAGE = new InjectionToken<Storage>('An abstraction over window.localStorage object', {
  factory: () => inject(WINDOW).localStorage,
});

export const WINDOW = new InjectionToken<Window>('An abstraction over global window object', {
  factory: () => {
    const { defaultView } = inject(DOCUMENT);

    if (!defaultView) {
      throw new Error('Window is not available');
    }

    return defaultView;
  },
});
