import { NgModule } from '@angular/core';
import type { Routes } from '@angular/router';
import { PreloadAllModules, RouterModule } from '@angular/router';

const routes: Routes = [
  {
    path: 'users',
    loadChildren: () => import('./pages/users/users.module').then((m) => m.UsersModule),
  },
  {
    path: 'driver-scorecard',
    loadChildren: () => import('./pages/advanced-driver/advanced-driver.module').then((m) => m.AdvancedDriverModule),
  },
  {
    path: 'scorecards',
    loadChildren: () => import('./pages/score-metrics/score-metrics.module').then((m) => m.ScoreMetricsModule),
  },
  {
    path: 'order-management',
    loadChildren: () => import('./pages/orders/orders.module').then((m) => m.OrdersModule),
  },
  {
    path: 'shipment-management',
    loadChildren: () => import('./pages/shipments/shipments.module').then((m) => m.ShipmentsModule),
  },
  { path: 'zones', loadChildren: () => import('./pages/zones/zones.module').then((m) => m.ZonesModule) },
  {
    path: 'ride-analytic',
    loadChildren: () => import('./pages/dashboard/dashboard.module').then((m) => m.DashboardModule),
  },
  {
    path: 'new-ride', //'new-ride',
    loadChildren: () => import('./pages/new-ride/new-ride.module').then((m) => m.NewRideModule),
  },
];

@NgModule({
  imports: [
    RouterModule.forRoot(routes, {
      preloadingStrategy: PreloadAllModules,
      onSameUrlNavigation: 'reload',
    }),
  ],
  exports: [RouterModule],
})
export class AppRoutingModule {}
