/* eslint-disable @typescript-eslint/consistent-type-imports */
import type { HttpEvent, HttpHandler, HttpInterceptor, HttpRequest } from '@angular/common/http';
import { HttpResponse } from '@angular/common/http';
import { Injectable } from '@angular/core';
import type { Observable } from 'rxjs';
import { tap } from 'rxjs/operators';
import { AlertService } from './../../../shared/services/alert-service/alert-service.service';
import { ErrorHandlerService } from './error-handler-service/error-handler.service';
/**
 * Adds a default error handler to all requests.
 */
@Injectable({
  providedIn: 'root',
})
export class ErrorHandlerInterceptor implements HttpInterceptor {
  constructor(private errorService: ErrorHandlerService, private alertSerive: AlertService) {}
  intercept(request: HttpRequest<any>, next: HttpHandler): Observable<HttpEvent<any>> {
    return next.handle(request).pipe(
      tap((res) => {
        if (res instanceof HttpResponse) {
          let errorMessage = '';
          if (res.body instanceof Object) {
            if (res.body['IsSuccessful'] == false) {
              res.body.Errors.forEach((e: string) => {
                errorMessage += e + '<br>';
              });

              this.alertSerive.showAlert('warning', errorMessage, 'Warning');
            }
          }
        }
        //  this.errorService.handleBackendError({ status: res.body.status, result: res.body.status.toString() });
      })
    );
  }
}
