/* eslint-disable @typescript-eslint/consistent-type-imports */
import { DOCUMENT } from '@angular/common';
import { Inject, Injectable } from '@angular/core';
import { IndividualConfig, ToastrService } from 'ngx-toastr';

@Injectable({
  providedIn: 'root',
})
export class AlertService {
  constructor(protected toaster: ToastrService, @Inject(DOCUMENT) private _document: Document) {}

  showConfirmationDialog(msg: string, clickHandler: () => any) {
    const alertOptions: Partial<IndividualConfig> = {
      disableTimeOut: true,
      easing: 'ease-in-out',
      easeTime: '500',
      closeButton: true,
      enableHtml: true,
      toastClass: 'alert alert-successalert-with-icon',
      positionClass: 'toast-top-center',
      progressBar: true,
    };
    /* const icon =
      '<span class="fas fa-exclamation-triangle" [data-notify]="icon"></span> '; */
    const confirmationDialog = this.toaster.error(
      `${msg} <a  id='confirmationRevertYes' class='btn btn-sm default bg-transparent streamDownload' (click)='clickHandler()'>Yes</a>`,
      'Confirm',
      alertOptions
    );

    confirmationDialog.onShown.subscribe(() => {
      this._document.getElementsByClassName('streamDownload')[0].addEventListener('click', () => {
        clickHandler();
      });
    });
  }

  /**
   * @showAlert
   * @param type :"danger" | "success" | "info" | 'warning'
   * @param message : Alert Message
   * @param title: Alert Title
   */
  showAlert(type: 'error' | 'success' | 'info' | 'warning', message = '', title = '') {
    const alertOptions = {
      disableTimeOut: false,
      closeButton: false,
      easing: 'ease-in-out',
      enableHtml: true,
      toastClass: 'alert alert-' + type + 'alert-with-icon',
      positionClass: 'toast-top-center',
      progressBar: true,
    };
    switch (type) {
      case 'error': {
        this.toaster.error(message, title, alertOptions);
        break;
      }
      case 'success': {
        this.toaster.success(message, title, alertOptions);
        break;
      }
      case 'warning': {
        this.toaster.warning(message, title, alertOptions);
        break;
      }
      case 'info': {
        this.toaster.info(message, title, alertOptions);
        break;
      }
    }
  }
  hideAlert() {
    this.toaster.clear();
  }
}
