/* eslint-disable @typescript-eslint/consistent-type-imports */
import type { HttpErrorResponse, HttpEvent } from '@angular/common/http';
import { Injectable } from '@angular/core';
import type { Observable } from 'rxjs';
import { AlertService } from 'src/app/shared/services/alert-service/alert-service.service';
import { Logger } from 'src/app/shared/services/logger-service/logger.service';

const log = new Logger('ErrorHandlerService');

@Injectable({
  providedIn: 'root',
})
export class ErrorHandlerService {
  constructor(protected toaster: AlertService) {}

  public handleError(err: HttpErrorResponse): Observable<HttpEvent<any>> {
    log.error('An error occurred:', err);
    let errorMessage = '';
    //? client-side or network error
    if (err.error instanceof ErrorEvent) {
      errorMessage = `An error has occurred: ${err.error.message}`;
    } else if (err.error instanceof ProgressEvent) {
      errorMessage = `An error has occurred: ${err.statusText}`;
    } else if (err.error instanceof Object) {
      if (err.error.errors && err.error.errors.length == 1) {
        errorMessage = `${err.error.errors[0]}`;
      } else {
        err.error.errors.forEach((e: string) => {
          errorMessage += '<br>' + e;
        });
      }
      log.error('An error occurred:', err.error.result);
      // this.toaster.showAlert('warning', errorMessage, 'Error')

      // errorMessage = `${err.error.result}`;
    } else {
      //? server-side error
      switch (err.status) {
        case 400:
          // eslint-disable-next-line no-undef
          console.log(err);
          errorMessage = `${err.status}: Bad Request.`;
          break;
        case 401:
          errorMessage = `${err.status}: You are unauthorized to do this action.`;
          break;
        case 403:
          errorMessage = `${err.status}: You don't have permission to access the requested resource.`;
          break;
        case 404:
          errorMessage = `${err.status}: The requested resource does not exist.`;
          break;
        case 412:
          errorMessage = `${err.status}: Precondition Failed.`;
          break;
        case 500:
          errorMessage = `${err.status}: Internal Server Error.`;
          this.toaster.showAlert('error', errorMessage, 'Error');
          break;
        case 503:
          errorMessage = `${err.status}: The requested service is not available.`;
          break;
        default:
          errorMessage = 'oops! Something went wrong';
      }
    }
    // this.toaster.showAlert('error', errorMessage, 'Error')

    // this.alertService.showAlert('error', 'Error', errorMessage ?? 'oops! Something went wrong', 'ok');
    throw err;
  }

  public handleBackendError(err: { status: number; result: string }) {
    log.info(err);
    this.toaster.showAlert('error', err.result, 'Error');
  }
}
