import { Component, ChangeDetectionStrategy, Input } from '@angular/core';

@Component({
  selector: 'app-circle-icon',
  template: `
    <ng-container *ngIf="scale === 'large'; else small">
      <svg xmlns="http://www.w3.org/2000/svg" width="24" height="24" viewBox="0 0 24 24">
        <circle
          style="fill:{{ fillColor }};"
          id="Ellipse_30"
          data-name="Ellipse 30"
          class="grncrl-1"
          cx="12"
          cy="12"
          r="12"
        />
      </svg>
    </ng-container>
    <ng-template #small>
      <svg xmlns="http://www.w3.org/2000/svg" width="8" height="8" viewBox="0 0 8 8">
        <circle data-name="Ellipse 26" cx="4" cy="4" r="4" style="fill:{{ fillColor }};" />
      </svg>
    </ng-template>
  `,
  styles: [``],
  changeDetection: ChangeDetectionStrategy.OnPush,
})
export class CircleIconComponent {
  @Input() fillColor = '#09a709';
  @Input() scale: 'large' | 'small' = 'large';
  constructor() {}
}
